.NavbarItems {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
    width: 90%;
    height: 80px;
    border-radius: 13px;
    margin-left: 2.5%;
    margin-top: 0px;
    /* position: absolute; */
    background-color: white;
    opacity: 100%;
    margin-top: 21px;
}
  
  .navbar-logo {
    color: #222;
    font-size: 2rem;
    cursor: pointer;
  }
  
  .nav-menu {
    display: flex;
    list-style: none;
    align-items: center;
  }
  
  .nav-links {
    text-decoration: none;
    color: #222;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space: nowrap;
  }
  .nav-s {
    text-decoration: none;
    color: #222;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space: nowrap;
  }
  
  .nav-links i {
    padding-right: 10px;
  }
  
  .nav-links:hover {
    background-color: #01959a;
    color: #fff;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
  }
  
  .nav-links-mobile {
    display: none;
  }
  .nav-s-mobile {
    display: none;
  }
  
  button {
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    transition: 0.2s ease-in-out;
  }
  
  button:hover {
    background-color: #151516;
    color: #fff;
  }
  
  .menu-icons {
    display: none;
  }
  
