.crt{
    margin: 1rem 4rem;
}

@media screen and (max-width:600px) {
    .crt{
        margin: 0;
        /* height: 40vh; */
    }
    .containerdivs {
        display: grid;
        padding: 0 5%;
        margin-top: 20px;
    }
    .marg {
        width: 200px;
        /* background-color: red; */
        display: flex;
        justify-content: flex-start;
        margin-right: -7vw;
        font-weight: 500;
        margin-bottom: 7px;
    }
    element.style {
        display: inline-flex;
        width: 50px;
        height: 20px;
        position: relative;
        left: -41%;
    }
    element.style {
        height: 20px;
        width: 85%;
        background-color: rgb(224, 224, 222);
        border-radius: 50px;
        margin: 10px 8%;
    }
}